interface TransactionData {
  checkoutId: string;
  transactionId: string;
  status: string;
  total: number;
  currency: string;
  items: Array<{
    price: {
      product_id: string;
      description?: string;
    };
  }>;
  paddleEventData: any;
}

export const PADDLE_PLANS = {
  'pri_01jqxf4pd45rpn0h4t': {
    name: 'Standard Plan',
    features: ['Basic features', 'Email support']
  },
  'pri_01jqxf4pd45rpn0h4t2': {
    name: 'Premium Plan',
    features: ['All features', 'Priority support']
  },
  'pro_01jrcyajvbkf83y5ycbnr055hf': {
    name: 'Premium Monthly',
    features: ['All features', 'Priority support']
  }
} as const;

export const identifyPlan = (productId: string | null) => {
  if (!productId) return { name: 'Free Plan', features: ['Basic access'] };
  return PADDLE_PLANS[productId as keyof typeof PADDLE_PLANS] || { name: 'Unknown Plan', features: [] };
};

export const getTransactionDetails = (transaction: TransactionData) => {
  const productId = transaction.items?.[0]?.price?.product_id;
  const plan = identifyPlan(productId);
  
  return {
    ...plan,
    amount: `${transaction.total} ${transaction.currency}`,
    status: transaction.status,
    checkoutId: transaction.checkoutId,
    transactionId: transaction.transactionId,
    rawProductId: productId,
    purchaseDate: transaction.paddleEventData?.created_at,
  };
};

export interface CheckoutCompletedEvent {
  checkout_id: string;
  completed: boolean;
  customer_id: string;
  status: string;
  items?: Array<{
    price_id: string;
    quantity: number;
  }>;
}

export const extractSubscriptionInfo = async (event: any) => {
  // Handle different Paddle event types
  if (event.name === 'checkout.completed') {
    return {
      checkoutId: event.data.id,
      customerId: event.data.customer?.id,
      status: event.data.status,
      items: event.data.items,
      eventType: 'checkout.completed'
    };
  }
  
  if (event.name === 'subscription.created' || event.name === 'subscription.updated') {
    return {
      subscriptionId: event.data.id,
      customerId: event.data.customer_id,
      status: event.data.status,
      items: event.data.items,
      nextBillDate: event.data.next_billed_at,
      eventType: event.name
    };
  }
  
  if (event.name === 'subscription.canceled') {
    return {
      subscriptionId: event.data.id,
      customerId: event.data.customer_id,
      status: 'canceled',
      canceledAt: event.data.canceled_at,
      eventType: 'subscription.canceled'
    };
  }
  
  return null;
};

// Function to sync subscription data between client and server
export const syncSubscriptionWithServer = async (customerId: string, userId: string) => {
  try {
    const response = await fetch('/api/subscriptions/get-details', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({ customerId, userId }),
    });
    
    if (!response.ok) {
      throw new Error('Failed to fetch subscription details');
    }
    
    return await response.json();
  } catch (error) {
    console.error('Error syncing subscription:', error);
    return null;
  }
};
